<template>
    <div class="date-picker" v-click-outside>
        <input type="text" :value="formatDate" />
        <div class="pannel" v-if="isVisible">
            <div class="pannel-nav">
                <span @click="prevYear">&lt;</span>
                <span @click="prevMonth">&lt;&lt;</span>
                <span>{{ time.year }}年</span>
                <span>{{ time.month+1 }}月</span>
                <span @click="nextMonth">&gt;&gt;</span>
                <span @click="nextYear">&gt;</span>
            </div>
            <div class="pannel-content">
                <div>
                    <span
                        v-for="j in 7" :key="`_`+j"
                        class="cell"                        
                    >
                        {{ weekDays[j-1] }}
                    </span>
                </div>
                
                <div v-for="i in 6" :key="i">
                    <span 
                        v-for="j in 7" :key="j"
                        class="cell cell-days"
                        @click="chooseDate(visibeDays[(j-1) + (i-1) * 7])"
                        :class="[
                            {notCurrentMonth: !isCurrentMonth(visibeDays[(j-1) + (i-1) * 7])},
                            {today: isToday(visibeDays[(j-1) + (i-1) * 7])},
                            {select: isSelect(visibeDays[(j-1) + (i-1) * 7])}
                        ]"
                    >
                        {{ visibeDays[(j-1) + (i-1) * 7].getDate() }}
                    </span>
                </div>
            </div>
            <div class="pannel-footer"  @click="today">
                今天
            </div>
        </div>
    </div>
</template>
<script>
import * as Util from './util';
export default {
    directives: {
        clickOutside: {
            bind (el, bindings, vnode) {
                let handler = (e)=>{
                    if (el.contains(e.target)) {
                        if (!vnode.context.isVisible) {
                            vnode.context.focus();
                        }
                    } else {
                        if (vnode.context.isVisible) {
                            vnode.context.blur();
                        }
                    }
                }
                el.handler = handler;
                document.addEventListener('click', handler)
            },
            unbind (el) {
                document.removeEventListener('click', el.handler)
            }
        }
    },
    data(){
        let {year, month} = Util.getYearMonthDay(this.value);
        return {
            time: {year, month},
            weekDays: ['日', '一', '二', '三', '四', '五', '六'],
            isVisible: false
        }
    },
    props: {
        value: {
            type: Date,
            default: () => new Date()
        }
    },
    methods:{
        focus() {
            this.isVisible = true;
        },
        blur() {
            this.isVisible = false;
        },
        isCurrentMonth(date) {
            let {year, month} = Util.getYearMonthDay(Util.getDate(this.time.year, this.time.month, 1));
            let {year:y, month:m} = Util.getYearMonthDay(date);
            return year === y && month === m;
        },
        isToday(date) {
            let {year, month, day} = Util.getYearMonthDay(new Date());
            let {year:y, month:m, day:d} = Util.getYearMonthDay(date);
            return year === y && month === m && day === d;
        },
        isSelect(date) {
            let {year, month, day} = Util.getYearMonthDay(this.value);
            let {year:y, month:m, day:d} = Util.getYearMonthDay(date);
            return year === y && month === m && day === d;
        },
        chooseDate(date) {
            let {year, month} = Util.getYearMonthDay(date);
            this.time = {year, month};
            this.$emit('input', date);
            this.blur();
        },
        prevYear() {
            this.time.year--
        },
        prevMonth() {
            let d = Util.getDate(this.time.year, this.time.month, 1);
            d.setMonth(d.getMonth()-1);
            this.time = Util.getYearMonthDay(d);
        },
        nextMonth() {
            let d = Util.getDate(this.time.year, this.time.month, 1);
            d.setMonth(d.getMonth()+1);
            this.time = Util.getYearMonthDay(d);
        },
        nextYear() {
            this.time.year++
        },
        today() {
            let now = new Date()
            this.time = Util.getYearMonthDay(now);
            this.$emit('input', now);
            this.blur();
        }
    },
    computed: {
        formatDate () {
            let{year, month, day} = Util.getYearMonthDay(this.value)
            return `${year}-${month+1}-${day}`;
        },
        visibeDays () {
            let{year, month} = Util.getYearMonthDay(Util.getDate(this.time.year, this.time.month, 1));//this.value)
            let currentFirstDay = Util.getDate(year, month, 1)
            let week = currentFirstDay.getDay();
            let startday = currentFirstDay - week * 60 * 60 * 24 * 1000;
            let arr = [];
            for (let i=0; i < 42; i++) {
                arr.push(new Date(startday + i * 60 * 60 * 24 * 1000))
            }
            return arr;
        }
    }
}
</script>

<style>
.date-picker {
    position: relative;
}
.pannel {
    width: 175px;
    position:absolute;
    background-color: #fff;
    box-shadow: 1px 1px 2px rgb(42, 174, 187) , -1px -1px 2px rgb(42, 174, 187);
}
.pannel .pannel-nav {
    display: flex;
    justify-content: space-around;
    height: 25px;
    line-height: 25px;
}
.pannel .pannel-nav span {
    cursor: pointer;
    user-select: none;
}
.pannel .pannel-content .cell {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    font-weight: bold;
    box-sizing: border-box;
}
.pannel .pannel-content .cell-days:hover {
    border: 1px solid rgb(235, 158, 94);

}
.pannel .pannel-content .notCurrentMonth{
    color: #ccc;
}
.pannel .pannel-content .select{
    background-color: rgb(37, 80, 143);
    color: #fff;
    border-radius: 4px;
}
.pannel .pannel-content .today{
    background-color: rgb(227, 15, 38);
    color: #fff;
    border-radius: 4px;
}
.pannel .pannel-footer{
    height: 25px;
    line-height: 25px;
    text-align: center;
    font-weight: bold;
}
.pannel .pannel-footer:hover{
    cursor: pointer;
    user-select: none;
}
</style>
